.footer {
  width: 100%;
  margin: 0px;
  padding: 60px 0px 80px 0px;
  background-color: var(--main-color);
  position: relative;
}
.footer__container {
  margin: 0px;
  display: flex;
}
.footer__info {
  width: 100%;
  max-width: 400px;
  margin: 0px 40px 0px 0px;
}
.footer__title {
  margin: 0px 0px 12px 0px;
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  line-height: 54px;
  color: var(--white-color);
}
.footer__copy {
  margin: 44px 0px 0px 0px;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  color: var(--white-color);
}
.footer__nav {
  width: 100%;
  margin: 0px;
  display: flex;
  justify-content: space-between;
}
.footer__column {
  margin: 0px;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 180px;
}
.footer__column_type_large {
  width: 100%;
  max-width: 380px;
}
.footer__column-title {
  margin: 0px;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  color: var(--white-color);
}
.footer__link {
  margin: 12px 0px 0px 0px;
  padding: 0px 0px 0px 20px;
  position: relative;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: var(--white-color);
  text-decoration: none;
}
.footer__link:hover {
  cursor: pointer;
  font-weight: bold;
}
.footer__link::before {
  content: '';
  position: absolute;
  top: 6px;
  left: 0px;
  background-image: url('../../images/footer-arrow.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 8px;
  height: 12px;
}

@media all and (max-width: 768px) {
  .footer {
    width: 100%;
    margin: 0px;
    padding: 40px 0px 20px 0px;
    background-color: var(--main-color);
    overflow: hidden;
  }
  .footer__container {
    flex-direction: column;
    z-index: 10;
    position: relative;
  }
  .footer__info {
    width: 100%;
    max-width: none;
    margin: 0px 0px 60px 0px;
  }
  .footer__nav {
    display: flex;
    flex-direction: column;
  }
  .footer__column {
    margin: 0px;
    max-width: none;
  }
  .footer__column_type_large {
    margin: 20px 0px 0px 0px;
    max-width: none;
  }
  .footer__copy {
    position: relative;
    font-size: 16px;
    line-height: 20px;
    text-align: left;
    z-index: 10;
  }
}
